<style lang=less>
  @import '../../../assets/index.less';
  @import './resetPwd.less';
</style>
<template>
  <div class="base-info reset-pwd">
    <!-- 修改登录密码 -->
    <p class="title">
      <span></span>{{$t('center.resetPwd[0]')}}
    </p>
    <el-form  class="reset-pwd-form" :model="form">
      <el-form-item :label="$t('center.resetPwd[1]')">
        <el-input v-model="form.oldPwd" type="password" :placeholder="$t('center.resetPwd[2]')"></el-input>
      </el-form-item>
      <el-form-item :label="$t('center.resetPwd[3]')">
        <el-input v-model="form.pwd" type="password" :placeholder="$t('center.resetPwd[4]')"></el-input>
      </el-form-item>
      <el-form-item :label="$t('center.resetPwd[5]')">
        <el-input v-model="form.repeatPwd" type="password" :placeholder="$t('center.resetPwd[6]')"></el-input>
      </el-form-item>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.resetPwd[7]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {resetLoginPwd} from "@/api/user.js"
export default {
  data () {
    return {
      form:{//表单中数据
        oldPwd:'',
        pwd:'',
        repeatPwd:''
      },
      submitLoading:false,
      sessionData:{},
    }
  },
  created () {
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
  },
  watch: {
  },
  methods: {
    submit(){//提交
      if(!this.$public.pwd(this.form.pwd)){
        this.$public.msg(this.$t('center.resetPwd[8]'),'warning',this)
        return
      }

      if(this.form.pwd != this.form.repeatPwd){
        this.$public.msg(this.$t('center.resetPwd[9]'),'warning',this)
        return
      }
      this.$public.googleVerify().then((r)=> {
        if(r){//验证通过
            this.submitRequest()
        } else{
            this.$public.msg(this.$t('center.resetPwd[10]'),'error',this)
        }
      }).catch((r)=>{
        console.log('取消')
      })

    },
    submitRequest(){
      this.submitLoading = true
      let _data = {
        account: JSON.parse(sessionStorage.getItem('account')),
        oldpwd: this.$public.CryptographicLand(this.form.oldPwd),
        opwd: this.$public.CryptographicLand(this.form.pwd),
        opwd1: this.$public.CryptographicLand(this.form.repeatPwd),
      }
      resetLoginPwd(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.resetPwd[11]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.form.oldPwd = ''
          this.form.pwd = ''
          this.form.repeatPwd = ''
        }
      })
    },
  }
}
</script>



